import React, { Component } from 'react';
import { Tag } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xtag extends Component {
	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	render() {
		const { defaultOptions} = this.props.configs

		return (
			<div id="x-time-picker">
				<h3>Tag 标签</h3>
				<div className="mt-20 mb-20">
					<Tag>标签一</Tag>
					<Tag type="gray">标签二</Tag>
					<Tag type="primary">标签三</Tag>
					<Tag type="success">标签四</Tag>
					<Tag type="warning">标签五</Tag>
					<Tag type="danger">标签六</Tag>
				</div>
				<Codemirror value={
					`<Tag>标签一</Tag>
<Tag type="gray">标签二</Tag>
<Tag type="primary">标签三</Tag>
<Tag type="success">标签四</Tag>
<Tag type="warning">标签五</Tag>
<Tag type="danger">标签六</Tag>`
				} options={defaultOptions} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(Xtag)
